* {
  margin: 0;
}

body {
  margin-bottom: 4em;
  background: url("/images/bitcoins.jpg") no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

html {
  position: relative;
  min-height: 100%;
  /* The html and body elements cannot have any padding or margin. */
}

body, label, .checkbox label {
  font-weight: 300;
}

.container {
  background-color: rgba(255, 255, 255, 0.85);
}

.container .container-fluid .row {
  margin-top: 3em;
}

#container-top {
  margin-top: 6em;
}

#faucet-iframe, #rotator-iframe {
  width: 100%;
  height: 50em;
  border: 2px solid #ddd;
}

.btn-width {
  width: 7em;
}

.btn-width-small {
  width: 5em;
}

.faucet-payment-processors {
  margin-left: -2em;
}

.accordion {
  min-width: 13em;
  max-width: 20em;
}

.table-responsive {
  overflow-x: auto;
}

.space-right {
  margin-right: 0.5em;
}

.button-font-size {
  font-size: 1.3em;
}

.button-font-small {
  font-size: 1em;
}

.admin-details {
  width: 40%;
}

/* Set the fixed height of the footer here */
.footer {
  top: 0;
  width: 100%;
  height: 4.4em;
}

.footer .container .center {
  text-align: center !important;
}

#twitter-config-fieldset {
  margin-bottom: 3.4em;
}

#disqus_thread {
  margin-bottom: 4.4em;
}

#faucets_table thead, #payment_processors_table thead {
  border: 1px solid #ddd;
}

#faucets_table thead th, #payment_processors_table thead th {
  height: 4em !important;
  padding-bottom: 1.25em !important;
  font-size: 0.9em;
}

#faucets_table thead th .headerSortDown, #payment_processors_table thead th .headerSortDown {
  background-image: url("../table_sorter_themes/blue/desc.gif");
}

#faucets_table thead th .headerSortUp, #payment_processors_table thead th .headerSortUp {
  background-image: url("../table_sorter_themes/blue/asc.gif");
}

#faucets_table thead td, #payment_processors_table thead td {
  border-right: 1px solid black !important;
}

#tags-table thead {
  border: 1px solid #ddd;
}

#tags-table thead th {
  height: 4em !important;
  padding-bottom: 1.25em !important;
  font-size: 0.9em;
}

#tags-table thead td {
  border-right: 1px solid black !important;
}

#nav-container {
  margin-bottom: 0.5em;
}

#nav-container .center-block {
  float: none !important;
}

#nav-container #nav-list {
  text-align: center;
}

#nav-container #nav-list li {
  display: inline;
  list-style-type: none;
}

#page-heading, .page-heading, #comments {
  text-align: center;
  margin-top: 2.5em;
}

div #vote li {
  list-style-type: none !important;
}

fieldset.border {
  border: 2px groove #ddd !important;
  padding: 0 1.4em 1.4em 1.4em !important;
  margin: 0 0 1.5em 0 !important;
  -webkit-box-shadow: 0 0 0 0 #000;
  box-shadow: 0 0 0 0 #000;
}

legend.border {
  font-size: 1.2em !important;
  font-weight: bold !important;
  text-align: left !important;
  width: auto;
  padding: 0 10px;
  border-bottom: none;
}

#btc-quote {
  margin-left: 1em !important;
}

#submit, #adblock_config_form {
  margin-bottom: 5em;
}

#main_page_content {
  margin-bottom: 4em;
}

table.tablesorter-no-sort thead tr th, table.tablesorter-no-sort tfoot tr th {
  background-color: #e6EEEE;
  font-size: 8pt;
  padding: 4px;
}

table.tablesorter-no-sort thead tr .header {
  background-image: url("bg.gif");
  background-repeat: no-repeat;
  background-position: center right;
  cursor: pointer;
}

@media screen and (min-width: 20em) {
  #nav-container {
    margin-left: -3em !important;
  }
}

@media screen and (min-width: 48em) {
  #nav-container .btn-lg {
    width: 4em;
  }
}

@media screen and (min-width: 50em) {
  #nav-container .btn-lg {
    width: 4em;
  }
}

@media screen and (min-width: 64em) {
  #nav-container {
    margin-right: 1.6em !important;
  }
  #nav-container .btn-lg {
    width: 6em;
  }
}

@media screen and (min-width: 64.063em) {
  #nav-container {
    margin-left: -1.5em !important;
  }
}
